var options={
    sensorInfNow(unittype=[],splitNumber=[],min=[],max=[],len=[],data=[]){
        var series=[];//驱动图表的数据内容数组
        var serieslen=unittype.length;
        var centerAxisNum=50;
        var flooritemlen=3;
        var centerFun=function(len=0,index=1){
            var floor=Math.ceil(len/flooritemlen);
            var indexRemainder=index%flooritemlen;
            var remainder=len>=flooritemlen?flooritemlen:len%flooritemlen;
            return [`${centerAxisNum/remainder*((indexRemainder==0?flooritemlen:indexRemainder)*2-1)}%`,`${centerAxisNum/floor*(Math.ceil(index/flooritemlen)*2-1)}%`];
        }
        var radiusFun=function (len){
            return `${75/Math.ceil(len/flooritemlen)}%`
        }
        var titleFun=function (unittype,len){
            var title=[];
            var index=1;
            let floor=Math.ceil(len/flooritemlen);
            unittype.forEach(item=>{
                let indexRemainder=index%flooritemlen;
                let remainder=len>=flooritemlen?flooritemlen:len%flooritemlen;
                title.push({
                    text: item.name+'数据仪表图',
                    x: `${centerAxisNum/remainder*((indexRemainder==0?flooritemlen:indexRemainder)*2-1)}%`,
                    y:`${centerAxisNum/floor*(Math.ceil(index/flooritemlen)*2-2)}%`,
                    textStyle:{
                        align:'center'
                    },
                    textAlign: 'center'
                });
                index+=1;
            })
            return title;
        }
        for(var i=0;i<serieslen;i++){
            series.push({ 
            min: min[i] - len[i] / (splitNumber[i] - 4) * 2,
            max: max[i] + len[i] / (splitNumber[i] - 4) * 2,
            splitNumber: splitNumber[i],
            radius:radiusFun(serieslen),
            center:centerFun(serieslen,i+1),
            name: unittype[i].name,
            type: 'gauge',
            detail: {
                formatter: '{value} '+unittype[i].unit
            },
            axisLine: {
                show: true,
                lineStyle: {
                    width:15,
                    color: [
                        [2 / splitNumber[i], '#ec2a25'],
                        [(splitNumber[i] - 2) / splitNumber[i], ' #47e64f'],
                        [1, '#ec2a25']
                    ],
                },
            },
            data: [{
                value: data[i],
                name: "当前"+unittype[i].name
            }],
            markPoint: {
                clickable: false,
                symbol: 'circle',
                symbolSize: 5,
                data: [
                    //跟你的仪表盘的中心位置对应上，颜色可以和画板底色一样
                    {
                        x: 'center',
                        y: 'center',
                        itemStyle: {
                            color: '#FFF'
                        }
                    }
                ]
            },
            axisLabel: {
                show: true,
                formatter: function (value) {
                    return value.toFixed(1);
                },
                textStyle: {
                    color: 'auto'
                },
            },
            axisTick: {
                splitNumber: 15,
            },
            splitLine:{
                length:17,
               lineStyle:{
                color:'rgba(128, 128, 128, 0.5)',
               }
            }
        });
        }
        return {
            title:titleFun(unittype,serieslen),
            tooltip: { //鼠标悬浮交互时的信息提示
                formatter: "{a} <br/>{b} : {c} "+unittype.unit //内容格式
            },
            toolbox: {
                feature: {
                    // restore: {},
                    // saveAsImage: {}
                }
            },
            splitNumber: splitNumber, //仪表盘分隔段数
            series:series,
        };
    },
    /**
     * 
     * @param {Object} unittype 图表单位格式
     * @param {Array} xAxisData x轴坐标名称,一维数组
     * @param {Array} data 实际数据,二维数组
     */
    sensorInfHis(unittype=[],xAxisData=[],data=[],legnedData=[],dataZoom= {show: false,start : 0},title="数据历史记录曲线图"){
        var series=[];
        var serieslen=unittype.length;
        var zIndex=serieslen;
        for(var i=0;i<serieslen;i++){
            series.push({
                data: data[i],
                type: 'line',
                smooth: true,
                symbolSize: 8,
                tooltip:{
                    formatter: "{a} <br/>时间:{b}<br/>数据:{c} "+unittype[i].unit //内容格式
                },
                z:zIndex,
                name:legnedData[i],
                areaStyle: {}
            });
            zIndex-=1;
        }
        return {
            title: {
                text:   title,
                x: 'left'
            },
            tooltip: { //鼠标悬浮交互时的信息提示
            },
            toolbox:{
                show:true,
                feature : {
                    mark : {show: true},
                    dataView : {show: true, readOnly: false},
                    restore : {show: true},
                    saveAsImage : {show: true}
                }
            },
            dataZoom:dataZoom,
            legend: {
                data:legnedData
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: { //x轴
                name: "时间",
                boundaryGap: false,
                type: 'category',
                axisLabel: {
                    margin: 8
                },
                axisTick:{
                    show:true,
                    interval:0,
                },
                data: xAxisData,
            },
            yAxis: { // y轴
                name: '历史数据',
                type: 'value'
            },
            grid: { // 绘图网络
    
            },
            series: series
        }
    },
    
};
export default options


